import React from 'react'

export default function ProductDetailView({ singleProduct }) {
  let { images } = singleProduct
  return (
    <div className='max-w-[1320px] mx-auto grid grid-cols-[45%_auto] my-5'>
      <div className='border-1 border-[#ccc]'>
        <img width={'100%'} src={singleProduct.thumbnail} alt="" />
        <div className='flex gap-3 ml-[100px] mb-2'>
          {
            images.map((src, index) => {
              return(
                <img width={"25%"} className='border-1 border-[#ccc]' src={src} key={index} alt="" />
              )
            })
          }
        </div>
      </div>
    </div>
  )
}
